}

/* Header */

.BookSection_header__3TuEK {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 300px;
    color: #ffffff;
    color: var(--header-secondary-color);
    box-shadow: 0 5px 10px #ff864e;
    box-shadow: 0 5px 10px var(--header-primary-color);

    background-color: #ff864e;

    background-color: var(--header-primary-color);
    font-size: 75.5%;

    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}


.BookSection_headertext__2xWx0 {

    font-size: 2rem;
    margin-top: 0px;
    margin-bottom: 55px;
    font-style: italic;
}

