    font-size: 36px;
    padding-bottom: 20px;
}

.BookSection_search__2nN7x input {
    outline: 0;
    border: 0;
    width: 450px;
    height: 55px;
    padding: 0 0.5rem;
    font-size: 13px;
    font-weight: bold;
}

.BookSection_search__2nN7x button {
    outline: none;
    border: none;
    height: 55px;
    padding: 0 0.5rem;
}

.BookSection_search__2nN7x button:hover {
