    transition: .5s;
    text-align: center;
    position: relative;
}

.BookSection_card__5HYXB:hover {
    cursor: pointer;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    background-color: rgba(49, 46, 46, 0.8);
}

.BookSection_card__5HYXB img {
